.footer {
    width: 100%;
    text-align: center;
    background-color: #595959;
    padding: 1.4rem 0;
    position: fixed;
    bottom: 0;
}


.footerTitle {
    color: antiquewhite;
    margin-block-start: 0;
    margin-block-end: 0;
    font-weight: 400;
    font-size: 1.2rem;
}

.subtitle-footer {
    margin-block-start: 0;
    margin-block-end: 0;
    margin-top: 4px;
    color: rgb(255, 255, 255);
    letter-spacing: 1.5px;
}